import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'
import activityForNightClubDetail from '@/view/activity/activity-for-night-club-detail.vue';  //深夜俱乐部详情页

Vue.config.productionTip = false
Vue.use(Router)

const router = new Router({
  // base: '/activity-for-night-club-detail.html',
  routes: [{
    path: '/',
    name: 'activityForNightClubDetail',
    component: activityForNightClubDetail
  },]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})




